'use strict';

const tBody = document.querySelector('tbody');
const tHead = document.querySelector('thead');
const rows = tBody.querySelectorAll('tr');

let check;
let sorted;

function convertToNum(string) {
  let res = '';

  for (const char of string) {
    if (char !== ',' && char !== '$') {
      res += char;
    }
  }

  return res;
};

tHead.addEventListener('click', (e) => {
  check = e.target.cellIndex;
  console.log(rows, [...rows]);

  sorted = [...rows].sort((prev, next) => {
    // const a = prev.cells[check].innerText.replace(/[$,]/g, '');
    // const b = next.cells[check].innerText.replace(/[$,]/g, '');
    const a = convertToNum(prev.cells[check].innerText);
    const b = convertToNum(next.cells[check].innerText);

    return isNaN(a)
      ? a.localeCompare(b)
      : a - b;
  });

  console.log(tBody, ...sorted);

  tBody.append(...sorted);
});
